// Растяжка на главной странице
$(document).ready(function () {
    /* Слайдим текст растяжки, если не влез на экран
     * Первый раз стартуем при инициализации,
     * далее перезапускаем при ресайзе окна
     */
    checkStretchBanner();
    $(window).resize(checkStretchBanner);

    function checkStretchBanner () {
        const content = $('.stretch-banner__content');

        if (content.length > 0) {
            isOverflowed(content[0])
                ? buildStretchBanner()
                : removeStretchBanner();
        }
    }

    function isOverflowed (el) {
        return el.scrollWidth > $(window).width();
    }

    function buildStretchBanner () {
        const contents = $('.stretch-banner__content');
        const container = $('.stretch-banner');

        $(container).addClass('stretch-banner_close');

        if (contents.length === 1) {
            $(contents).clone()
                .insertAfter(contents);
        }
    }

    function removeStretchBanner () {
        const container = $('.stretch-banner');
        const contents = $('.stretch-banner__content');

        $(container).removeClass('stretch-banner_close');

        if (contents.length > 0 ) {
            $(contents[1]).detach();
        }
    }

    /* Тогглим адреса магазинов в
     * растяжке, если их несколько
     */
    getStreetTopBanner();

    function getStreetTopBanner () {
        const isStretchBanner = $('#getStreetTopBanner')
            .length > 0;
        const isSomePlaces = $('.stretch-banner__location.active')
            .parent()
            .children('.stretch-banner__location')
            .length > 1;

        if (isStretchBanner && isSomePlaces) {
            setInterval(toggleSwitcherLocations, 10000);
        }
    }

    function toggleSwitcherLocations() {
        const newLocationIndex = getNewLocationIndex();

        hideActivePlaces();

        setTimeout(() => {
            resetActivePlaces();
            activeNewPlace(newLocationIndex);
        }, 300);

        setTimeout(showActivePlace, 400);
    }

    function getNewLocationIndex() {
        const location = $('.stretch-banner__location.active')[0];

        const countPlaces = $(location)
            .closest('.stretch-banner__content')
            .children('.stretch-banner__location')
            .length;

        return $(location).index() === countPlaces - 1
            ? 0
            : $(location).index() + 1;
    }

    function hideActivePlaces() {
        $('.stretch-banner__location').removeClass('visible');
    }

    function resetActivePlaces() {
        $('.stretch-banner__location').removeClass('active');
    }

    function activeNewPlace(idx) {
        const containers = $('.stretch-banner__content');

        containers.each((i, el) => {
            const newActivePlace = $(el)
                .children('.stretch-banner__location')[idx];

            $(newActivePlace).addClass('active');
        });
    }

    function showActivePlace() {
        $('.stretch-banner__location.active')
            .addClass('visible');
    }
});

