// Scripts for quiz test
/* At the super global variable STORE
 * save all data from user.
 * Using proxy for listening, update
 * and logging changes of data.
 */
window.STORE = {
    name: undefined,
    birthday: undefined,
    job: undefined,
    questions: {}
};

STORE = new Proxy(STORE, {
    set: function (target, prop, val) {
    
        if (val === '') val = undefined;
        
        target[prop] = val;
        
        return true;
    },
    
    deleteProperty: function(target, prop) {
        delete target[prop];
        
        return true;
    }
});

// Set user data
const userControllerList = [
    ...Array.from(document
        .querySelectorAll('.quiz-test__question [type="text"]')),
    ...Array.from(document
        .querySelectorAll('.quiz-test__question [type="tel"]'))
];

userControllerList.forEach(el => {
    el.addEventListener('input', updateUser);
});

function updateUser() {
    STORE[this.name] = this.value;
}

// Go to next test step
const goToNextButtons = Array.from(document.querySelectorAll(
    '.quiz-test__button:not(.quiz-test__button_end)'));

goToNextButtons.forEach(el => {
    el.addEventListener('click', goToNextStep);
});

function goToNextStep () {
    isGettingUserData(this)
        ? checkUserData(this)
        : checkAnswerCount(this);
}

function isGettingUserData(node) {
    const userForm = node.parentNode
        .querySelectorAll('.quiz-test__form');
    
    return userForm.length > 0;
}

function setUserError(node) {
    node.parentNode.classList.add('has-error');
}

function removeUserError(node) {
    node.parentNode.classList.remove('has-error');
}

function checkUserData(node) {
    let isFromValid = true;
    
    const controllers = node.parentNode
        .querySelectorAll('input') ;
    
    controllers.forEach(el => {
        if (el.value == '') {
            setUserError(el);
            isFromValid = false;
        } else {
            removeUserError(el);
        }
    });
    
    if (isFromValid) toggleStep(node);
}

function checkAnswerCount(node) {
    const container = node
        .closest('.quiz-test__question');
    
    const answerCountNeed = parseInt(container
        .dataset.answersCount);
    
    const answerCountSelected = container
        .querySelectorAll('input:checked').length;

    if (answerCountNeed > answerCountSelected) {
        alert("Выбрано недостаточно ответов!")
        return;
    }
    
    toggleStep(node);
}

function toggleStep(node) {
    const currentStep = node.parentNode;
    const currentStepNumber = currentStep.dataset.quizTestStep;
    const nextStepNumber = parseInt(currentStepNumber) + 1;
    
    const nextStep = document.querySelector(
        `[data-quiz-test-step="${nextStepNumber}"]`);
    
    currentStep.classList.remove('visible');
    nextStep.classList.add('visible');

    updateTestTitle(nextStepNumber);
    scrollToTestStart();
}

function updateTestTitle(stepNumber) {
    const numberPlace = document
        .getElementById('quizTestStep');
    
    if (stepNumber === 4) stepNumber = 3;
    
    numberPlace.innerText = stepNumber + '/3'
}

function scrollToTestStart() {
    const target = document
        .querySelector('.quiz-test__wrapper');
    
    target.scrollIntoView();
}

// Set answers to STORE
const testControllerList = Array.from(document
    .querySelectorAll('.quiz-test__card [type="checkbox"]'));

testControllerList.forEach(el => {
    el.addEventListener('input', setAnswer);
});

function setAnswer() {
    const container = this.closest('.quiz-test__question');
    const questionId = container.dataset.quizQuestion;
    const questionText = container.querySelector(
        '.quiz-test__caption').textContent;
    
    addQuestionInStore(questionId, questionText);
    toggleQuestionValue(this.checked, questionId, this.value);
    checkQuestionInStore(questionId);
}

function addQuestionInStore(id, text) {
    if (!STORE.questions[id]) {
        STORE.questions[id] = {
            text: text,
            values: []
        }
    }
}

function checkQuestionInStore(id) {
    if (!STORE.questions[id].values.length) {
        delete STORE.questions[id];
    }
}

function toggleQuestionValue(checked, questionId, value) {
    if (checked) {
        STORE.questions[questionId]
            .values.push(value);
    } else {
        const values = STORE.questions[questionId].values;
        const idx = values.indexOf(value);
        
        STORE.questions[questionId].values = [
            ...values.slice(0, idx),
            ...values.slice(idx + 1)
        ];
    }
}

